.timeline .timeline-wrap {
  max-width: 620px;
  margin: 0 auto;
}

.timeline__showmore{
  border: 1px solid var(--primary-dark);
  display: block;
  width: 100%;
  background-color: transparent;
  padding: 20px 0;
  position: relative;
  text-transform: uppercase;
  letter-spacing: 1.5px;

  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  font-weight: bolder;
  color: var(--primary-dark);
  cursor: pointer;
}
.timeline__showmore::before{
  content: " ";
  position: absolute;
  width: 100%;
  display: block;
  height: 240px;
  top: -241px;
  left: 0;
  right: 0;
  z-index: 2;
  background: rgb(255,255,255);
  background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.8) 50%);

  transition: 1.5s ease-out;
  -webkit-transition: 1.5s ease-out;
  -moz-transition: 1.5s ease-out;
  -ms-transition: 1.5s ease-out;
  -o-transition: 1.5s ease-out;
}
.timeline__showmore:hover::before{
  opacity: 0;
}
.timeline__showmore:hover{
  background-color: var(--primary);
  color: #000;
}