.header{
  text-align: center;
}
.logo{
  max-width: 35px;
}
.logo,
.logo path,
.logo polygon{
  fill: #000;
}
.header h1{
  text-transform: uppercase;
  letter-spacing: 3px;
  margin: 20px 0;
  font-size: 28px;
  font-weight: 900;
}
.header p {
  font-size: 18px;
  margin-bottom: 0;
}
.download-button{
  position: relative;
  padding-right: 35px;
  display: inline-block;
  min-width: 50px;
}
.download-button:hover{
  text-decoration: none;
}
.download-button:hover > span{
  text-decoration: underline;
}
.download-icon{
  width: 18px;
  height: 18px;
  position: absolute;
  top: calc(50% - (18px / 2));
  right: 8px;
}