.theme-switcher{
  display: inline-block;
  position: fixed;
  right: 30px;
  bottom: 30px;
  z-index: 9999999999;
}
.theme-switcher__button{
  height: 22px;
  width: 22px;
  border-radius: 20px;
  background-color: #ffe600;
  outline: 5px solid rgb(255, 230, 0, 0.5);
  cursor: pointer;
}
.theme-switcher__button.is-dark{
  background-color: #00d9ff;
  outline: 5px solid rgb(0, 217, 255, 0.5);
}