.work-item__details{
  padding: 15px;
}
.work-item__head{
  display: flex;
}
.work-item__info{
  flex: 1 0 auto;
}
.work-item__info h4{
  padding: 0;
  margin: 3px 0;
  font-size: 20px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.work-item__actions a,
.work-item__actions button{
  font-size: 12px;
  text-transform: lowercase;
  padding: 2px 4px;
  min-width: 60px;
  margin-left: 10px;
  display: inline-block;
  text-decoration: none;
}
.work-item__date span{
  font-size: 12px;
  font-weight: bolder;
  opacity: 0.7;
}
.work-item__categoriestags label{
  display: inline-block;
  padding: 4px 6px;
  background-color: #000;
  border-radius: 5px;
  margin-right: 5px;
  color: #fff;
  font-size: 14px;
  font-weight: bolder;
  line-height: 1.2;
  text-transform: lowercase;
}
.work-item__images{
  min-height: 50%;
}
.work-item__images img{
  max-width: 100%;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  border-radius: 10px;

  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}
