.skills-item{
  width: 100%;
  flex: calc(50% - 20px);
}
.skill-card:hover{
  cursor: pointer;
  background-color: rgba(128, 128, 128, 0.2);
}
.skill-item-head{
  margin-bottom: 5px;
}
.skill-item-progress{
  position: relative;
}
.skill-name{
  font-weight: bolder;
  text-transform: uppercase;
  margin-right: 20px;
  font-size: 16px;
}
.skill-percent{
  float: right;
  font-size: 16px;
  display: none;
}
.skill-card:hover .skill-percent{
  display: inline;
}
.skill-progress{
  position: relative;
  height: 15px;
  border-radius: 15px;
  width: 100%;
  background-color: var(--primary-light);
  overflow: hidden;
}
.skill-progress > span{
  position: absolute;
  display: inline-block;
  top: 0;
  left: 0;
  bottom: 0;
  height: 100%;
  border-radius: 15px;
  background-color: var(--primary-dark);
  box-shadow: 0 0 20px rgba(0,0,0,0.4);
}

.skill-details__set{
  padding: 15px;
}

.skill-details__head{
  display: flex;
}
.skill-details__title{
  flex: 1 0 auto;
}
.skill-details__title h4{
  padding: 0;
  margin: 3px 0;
  font-size: 20px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.skill-details__action a,
.skill-details__action button{
  font-size: 12px;
  text-transform: lowercase;
  padding: 2px 4px;
  min-width: 60px;
  margin-left: 10px;
  display: inline-block;
  text-decoration: none;
  position: relative;
  top: 4px;
}
.skill-details__content .skill-card{
  margin-top: 10px;
  cursor: default;
}
.skill-details__content .skill-name{
  text-transform: none;
  letter-spacing: 0;
  font-weight: normal;
}
.skill-details__t{
  display: flex;
  align-content: center;
  align-items: center;
}
.skill-details__t-c-1{
  flex: 0 0 50px;
}
.skill-details__t-c-1 img{
  max-width: 30px;
  max-height: 30px;
  object-fit: contain;
  display: block;
}
.skill-details__t-c-2{
  flex: 1 1 auto;
}

@media (max-width: 767px) {
  .skill-card{
    margin-bottom: 10px;
  }
  .skills-item{
    flex: calc(100% - 20px);
  }
  .skill-name{
    font-size: 14px;
  }
  .skill-details__title h4{
    font-size: 14px;
    position: relative;
    top: 4px;
  }
  .skill-card:hover .skill-percent,
  .skill-percent{
    float: none;
    font-size: 12px;
    display: block;
    font-weight: 900;
  }
}