@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,400;1,700&display=swap");

:root{
  --primary: #72f5c9;
  --primary-dark: #2e9271;
  --primary-light: #b7efdc;

  --danger: #eb4f4f;
}

html{
	background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
	animation: gradient 7s ease infinite;
	-webkit-animation: gradient 7s ease infinite;
	-moz-animation: gradient 7s ease infinite;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
@-webkit-keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
@-moz-keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

body {
  margin: 10px;
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  letter-spacing: 1px;
  color: #000;
  background-color: #fff;
  /* border: 3px solid #000; */
  border-radius: 14px;
}
body.modal-open{
  overflow: hidden;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.container{
  max-width: 850px;
  margin: 0 auto;
  padding: 0 20px;
}
.container.container-large{
  max-width: 1350px;
}
h2.heading{
  font-size: 24px;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 3px;
  position: relative;
  padding-bottom: 25px;
}
h2.heading::before{
  content: " ";
  display: inline-block;
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: #000;
  border-radius: 20px;
  bottom: 6px;
  left: calc(50% - (10px / 2));
}
.btn {
  border: 3px solid #000;
  background-color: transparent;
  padding: 10px;
  font-size: 16px;
  font-weight: bolder;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  cursor: pointer;
  text-align: center;
  min-width: 120px;
  line-height: 1;
}
.btn:hover {
  background-color: var(--primary);
  color: #000;
  text-decoration: underline;

  transition: 0.3s ease-out;
  -webkit-transition: 0.3s ease-out;
  -moz-transition: 0.3s ease-out;
  -ms-transition: 0.3s ease-out;
  -o-transition: 0.3s ease-out;
}
.btn.btn-danger{
  color: var(--danger);
}
.btn.btn-danger:hover{
  background-color: var(--danger);
  color: #fff;
}
@media (max-width: 767px) {
  .btn-mobile{
    display: inline-block;
    padding: 20px 10px !important;    
    border-radius: 10px;
  }
  .btn-mobile:hover{
    background-color: rgba(128,128,128,0.3);
  }
}
a {
  color: var(--primary-dark);
}
a:hover{
  color: var(--primary);
}

/*
======================================
DARK MODE
======================================
*/
body.dark-theme{
  background-color: #1d1d1d;
  color: #e2e2e2;
}
body.dark-theme .btn{
  border: 3px solid #e2e2e2;
  color: #e2e2e2;
}
body.dark-theme .btn:hover{
  color: #1d1d1d;
}
body.dark-theme .dark-theme_bg-white,
body.dark-theme h2.heading::before{
  background-color: #e2e2e2 !important;
}
body.dark-theme .dark-theme_bg-dark{
  background-color: #1d1d1d !important;
}
body.dark-theme .dark-theme_txt-dark{
  color: #1d1d1d !important;
}
body.dark-theme .dark-theme_txt-white{
  color: #e2e2e2 !important;
}
body.dark-theme .dark-theme_white-fill path,
body.dark-theme .dark-theme_white-fill polygon,
body.dark-theme .dark-theme_white-fill{
  fill: #e2e2e2 !important;
}
body.dark-theme .dark-theme_timeline::before{
  background: rgb(29,29,29) !important;
  background: linear-gradient(180deg, rgba(29,29,29,0) 0%, rgba(29,29,29,0.8) 30%) !important;
}
