.socials{
  margin: 20px 0;
  padding: 0;
}
.socials .socials__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: inline-block;
}
.socials .socials__list > span,
.socials .socials__list > a{
  display: inline-block;
  padding: 0 8px;
  color: var(--primary-dark);
  cursor: pointer;
}
.socials .socials__list > span:hover,
.socials .socials__list > a:hover{
  color: var(--primary);
  color: rgba(128,128,128, 0.5);
}
.socials .socials__list > .icon{
  width: 20px;
  height: 20px;
}
.icon{
  fill: var(--primary-dark);
}
.social__list-more{
  position: relative;
}
.socials__more{
  position: absolute;
  background-color: #fff;
  z-index: 2;
  border: 1px solid rgba(128,128,128, 0.5);
  white-space: nowrap;
  overflow: hidden;
}
.socials__more ul{
  margin: 0;
  padding: 0;
}
.socials__more ul li{
  list-style: none;
  text-align: left;
}
.socials__more ul li a{
  display: block;
  padding: 10px;
  border-bottom: 1px solid rgba(128,128,128, 0.5);
  text-decoration: none;
  line-height: 1;
  font-size: 14px;
}
.socials__more ul li a:hover{
  background-color: rgba(128,128,128, 0.5);
  color: #000;
}
.socials__more ul li:last-child a{
  border-bottom: none;
}
.socials__more ul li .icon{
  width: 15px;
  height: 15px;
  position: relative;
  top: 1px;
}
@media (max-width: 767px){
  .socials__more {
    right: 0;
  }
}