.timeline-item {
  position: relative;
  padding: 0 0 20px 20px;
}
.timeline-item:last-child {
  padding-bottom: 40px;
}
.timeline-item p {
  margin: -3px 0 0 0;
  line-height: 1.5;
  font-size: 14px;
}
.timeline-item p:before {
  content: " ";
  position: absolute;
  width: 9px;
  height: 9px;
  background-color: var(--primary-dark);
  border: 3px solid #fff;
  border-radius: 50px;
  left: -7px;
  top: 3px;
}
.timeline-item img {
  width: 30px;
  height: 30px;
  margin: 4px 0 0 0;

  object-fit: cover;
  -webkit-object-fit: cover;
  -moz-object-fit: cover;
}
.timeline-item ul {
  margin: 10px 0 10px 8px;
  padding: 0;
}
.timeline-item ul li {
  list-style: none;
  font-style: italic;
  font-size: 14px;
  margin: 4px 0 4px 0;
  opacity: 0.7;
}
.timeline-item h4 {
  opacity: 0.3;
  margin: 5px 0;
  font-size: 12px;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .timeline-item p,
  .timeline-item ul li {
    font-size: 12px;
  }
}