.timeline-section {
  display: flex;
  margin: 0 auto;
}
.timeline-section .year {
  position: relative;
}
.timeline-section h3 {
  position: sticky;
  position: -webkit-sticky;
  padding-right: 20px;
  top: 20px;
  font-size: 18px;
  margin: -2px 0 0 0;
  width: 80px;
  font-weight: 900;
}
@media only screen and (max-width: 767px) {
  .timeline-section h3 {
    font-size: 16px;
    width: 60px;
  }
}
@media only screen and (max-width: 370px) {
  .timeline-section h3 {
    font-size: 12px;
    width: 30px;
    margin-top: 2px;
  }
}
.timeline-items {
  position: relative;
}
.timeline-items::before {
  content: " ";
  position: absolute;
  width: 1px;
  height: 100%;
  background-color: var(--primary-dark);
  top: 0;
  left: 0;
}